<template>
  <div>
  <Loading :active.sync="isLoading"></Loading>
    <table class="table">
        <thead>
          <th>Email</th>
          <th width="80"></th>
          <th width="200"></th>
          <th></th>
          <th></th>
          <th></th>
        </thead>
        <tbody>
          <tr v-for="i in orders" :key="i.id">
            <td>{{i.user.email}}</td>
            <td>{{i.user.name}}</td>
            <td>{{i.user.address}}</td>
            <td>{{i.user.tel}}</td>
            <td>{{i.total |currency}}</td>
            <td><span v-if="i.is_paid"></span><span v-else></span></td>
            <td>
             <ul class="list-unstyled">
               <li v-for="(product, i) in i.products" :key="i">
                 {{ product.product.title }} {{ product.qty }}
                 {{ product.product.unit }}
               </li>
             </ul>
           </td>
          </tr>
        </tbody>    
    </table>
  </div>
</template>

<script>
export default {
  name: 'Orders',
  data () {
    return {
        orders :[],
        isLoading:false,
    }
  },
  methods:{
      getOrders (){
        //   orders api
          const api =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders`
          const vm = this;
          vm.isLoading=true;
          vm.$http.get(api).then((response)=>{
              vm.orders=response.data.orders;
              vm.isLoading=false;
          });
      },
      // updateOrder(id){
      //   console.log('click');
      //   console.log(id);
      //   const vm = this;
      //   const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/order/${id}`
      //  vm.$http.put(url).then((response)=>{
      //         console.log(response);
      //     });
      // }
  },
  created(){
      this.getOrders();
  }
}
</script>




