<template>
    <div>
        <Loading :active.sync="isLoading"></Loading>
        <div class="row mt-4">
            <div v-for="(i,k) in products" class="col-md-4 mb-4" :key="i.id">
            <div  class="card h-100 border-0 shadow-sm">
                <div style="height: 150px; background-size: cover; background-position: center" :style="{backgroundImage:`url(${i.imageUrl})`}"></div>
                <div class="card-body">
                <span class="badge badge-secondary float-right ml-2">{{i.category}}</span>
                <h5 class="card-title">
                    <a href="#" class="text-dark">{{i.title}}</a>
                </h5> 
                <p class="card-text">{{i.description}}</p>
                <div class="d-flex justify-content-between align-items-baseline">
                    <!-- <div class="h5">2,800 </div> -->
                    <div v-if="!i.price" class="h5">{{i.origin_price}} </div>
                    <del v-if="i.price" class="h6"> {{i.origin_price}} </del>
                    <div v-if="i.price" class="h5"> {{i.price}} </div>
                </div>
            </div>
                <div class="card-footer d-flex">
                    <button @click="getProduct(i.id)" type="button" class="btn btn-outline-secondary btn-sm">
                        <i v-if="status.loadingItem == i.id" class="fas fa-spinner fa-spin"></i>
                        
                    </button>
                    <button @click="addToCart(i.id,i.qty)" type="button" class="btn btn-outline-danger btn-sm ml-auto">
                        <i v-if="status.loadingItem==i.id" class="fas fa-spinner fa-spin"></i>
                        
                    </button>
             </div>
            </div>
            </div>
        </div>
  <table class="table">
        <thead>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </thead>
        <tbody>
          <tr v-for="item in cart.carts">
            <td class="align-middle">
              <button @click="deleteCart(item.id)" type="button" class="btn btn-outline-danger btn-sm">
                <i class="far fa-trash-alt"></i>
              </button>
            </td>
            <td class="align-middle">
              {{ item.product.title }}
              <div class="text-success" v-if="item.coupon">
                
              </div>
            </td>
            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
            <td class="align-middle text-right">{{ item.final_total }}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="3" class="text-right"></td>
            <td class="text-right">{{ cart.total }}</td>
          </tr>
          <tr v-if="cart.total!=cart.final_total">
            <td colspan="3" class="text-right text-success"></td>
            <td class="text-right text-success">{{ cart.final_total }}</td>
          </tr>
        </tfoot>
  </table>
<div class="input-group mb-3 input-group-sm">
  <input v-model="couponCode" type="text" class="form-control" placeholder="">
  <div class="input-group-append">
    <button @click="addCoupon" class="btn btn-outline-secondary" type="button">
      
    </button>
  </div>
</div>
        <!-- Modal -->
<div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
         <img :src="product.imageUrl" class="img-fluid" alt="">
             <blockquote class="blockquote mt-3">
               <p class="mb-0">{{ product.content }}</p>
               <footer class="blockquote-footer text-right">{{ product.description }}</footer>
             </blockquote>
             <div class="d-flex justify-content-between align-items-baseline">
               <div class="h4" v-if="!product.price">{{ product.origin_price }} </div>
               <del class="h6" v-if="product.price"> {{ product.origin_price }} </del>
               <div class="h4" v-if="product.price"> {{ product.price }} </div>
             </div>
              <select name="" class="form-control mt-3" v-model="product.num">
               <option :value="num" v-for="num in 10" :key="num">
                  {{num}} {{product.unit}}
               </option>
             </select>
      </div>
      <div class="modal-footer">
       <div class="text-muted text-nowrap mr-3">
                <strong>{{ product.num * product.price }}</strong> 
             </div>
        <button @click="addToCart(product.id,product.num)" type="button" class="btn btn-danger"></button>
      </div>
    </div>
  </div>
</div>

       <!--  -->
       <div class="my-5 row justify-content-center">
  <form class="col-md-6" @submit.prevent="createOrder">
    <div class="form-group">
      <label for="useremail">Email</label>
      <input type="email" class="form-control" name="email" id="useremail"
        v-model="form.user.email" placeholder=" Email" v-validate="'required|email'">
      <span class="text-danger">{{ errors.first('email') }}</span>
    </div>
  
    <div class="form-group">
      <label for="username"></label>
      <input :class="{'is-invalid':errors.has('name')}" type="text" class="form-control" name="name" id="username"
        v-model="form.user.name" placeholder="" v-validate="'required'">
      <span class="text-danger" v-if="errors.has('name')"></span>
    </div>
  
    <div class="form-group">
      <label for="usertel"></label>
      <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="">
    </div>
  
    <div class="form-group">
      <label for="useraddress"></label>
      <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
        placeholder="">
      <span class="text-danger"></span>
    </div>
  
    <div class="form-group">
      <label for="comment"></label>
      <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
    </div>
    <div class="text-right">
      <button class="btn btn-danger"></button>
    </div>
  </form>
</div>
    </div>
</template>

<script>

import $ from 'jquery';
export default {
  name: 'CustomOrder',
  data () {
    return {
        products :[],
        cart:[],
        isLoading:false,
        product:{},
        couponCode:"",
        status:{
            loadingItem:"",
        },
        form:{
          user:{
            name:'',
            email:'',
            tel:'',
            address:'',
          },
          message:'',
        }
    }
  },
  methods:{
      getProducts() {
          const vm = this;
          vm.isLoading = true;
          const url =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
          vm.$http.get(url).then((response)=>{
              vm.products= response.data.products;
              vm.isLoading=false;
              console.log(vm.products);
          })
          
      },
    //   modal
    getProduct(id){
        const vm = this ;
        vm.status.loadingItem=id ;
        const url =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`
        vm.$http.get(url).then((response)=>{
            vm.product = response.data.product;
            $("#productModal").modal('show');
            vm.status.loadingItem='';
        })
    },
    addToCart(id,qty=1){
        // qty = 
        const vm = this ;
        vm.isLoading=true;
        vm.status.loadingItem=id ;
        const url =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`
        const cart = {
            product_id:id,
            qty
        }
        console.log(cart);
        vm.$http.post(url,{data:cart}).then((response)=>{
            console.log(response);
            vm.status.loadingItem='';
            $('#productModal').modal('hide');
            vm.getCart();
            vm.isLoading=false;
        })
    },
    getCart(){
        const vm =this;
        vm.isLoading=true;
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
        vm.$http.get(url).then((response)=>{
            vm.cart = response.data.data;
            console.log(vm.cart);
            vm.isLoading=false;
        })
    },
    deleteCart(id){
        const vm = this ;
        vm.isLoading=true;
        const url =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`
        vm.$http.delete(url).then((response)=>{
            console.log(response);
            vm.getCart();
            vm.isLoading=false;
        })
    },
    addCoupon (){
         const vm = this ;
          vm.isLoading=true;
          const coupon = {
            code:vm.couponCode
          }
          const url =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`
          vm.$http.post(url,{data:coupon}).then((response)=>{
              console.log(response);
              vm.getCart();
              vm.isLoading=false;
        })
    },
    createOrder(){
      const vm = this ;
      vm.isLoading = true ;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      this.$validator.validate().then((result)=>{
        if(result){
          vm.$http.post(url,{data:vm.form}).then((response)=>{
          console.log("",response);
          console.log(response.data);
          if(response.data.success){
            vm.$router.push(`custom_checkout/${response.data.orderId}`);
          }
          vm.isLoading = false;
          });
        }else{
          console.log('');
        }
           
      })
    }
  },
  created(){
      this.getProducts();
      this.getCart();
  }
}
</script>


