<template>
    <div>
        <div class="row justify-content-center">
                
                <div class="col-md-8">
                    <h3 class="mb-3 text-center"></h3>
                    <div class="row">
                        <div class="col-md-4">
                            <div class="text-center alert alert-success" role="alert">
                                
                            </div>
                        </div>
                        <div class="col-md-4">
                            <div class="text-center alert alert-success"  role="alert">
                                
                            </div>
                        </div>
                        <div class="col-md-4">
                            <div class="text-center alert alert-dark"  role="alert">
                                
                            </div>
                        </div>
                    </div>
                    <table class="table">
                        <thead>
                            <th colspan="2">
                                <h2 class="text-center"></h2>
                            </th>
                        </thead>
                        <tbody>
                            <tr>
                            <th width="120">Email</th>
                            <td>{{order.user.email}}</td>
                            </tr>
                            <tr>
                            <th></th>
                            <td>{{order.user.name}}</td>
                            </tr>
                            <tr>
                            <th></th>
                            <td>{{order.user.tel}}</td>
                            </tr>
                            <tr>
                            <th></th>
                            <td>{{order.user.address}}</td>
                            </tr>
                            <tr>
                            <th></th>
                            <td>
                                <span v-if="!order.is_paid"></span>
                                <span v-else></span>
                            </td>
                            </tr>
                        </tbody>
                        <tfoot>
                            <tr>
                                <td colspan="1" class='align-middle'>
                                    <a href="#">
                                        <i class="fas fa-angle-left"></i>
                                        
                                    </a>
                                </td>
                                <td colspan="">
                                    <button @click="payOrder" class="btn btn-danger btn-block"></button>
                                </td>
                            </tr>
                        </tfoot>
                    </table>
                </div>
            
            </div>
    </div>
</template>
<script>
    export default{
        data(){
            return {
                orderId:"",
                order:{
                    user:{}
                }
            }
        },
        methods:{
            getOrder(){
                const vm = this ;
                vm.$store.dispatch('updateLoading',true);
                const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`
                vm.$http.get(url).then((response)=>{
                    this.order = response.data.order;
                    vm.$store.dispatch('updateLoading',false);
                })
            },
            payOrder:function(){
            const vm = this;
            vm.$store.dispatch('updateLoading',true);
            const url =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
            vm.$http.post(url).then((response)=>{
                console.log(response);
                if(response.data.success){
                    vm.getOrder();
                    vm.$router.push('/customer_website/checkout_success');
                }else{
                    this.$bus.$emit('messsage:push',response.data.message,'danger');
                }
                vm.$store.dispatch('updateLoading',false);
          })
        }
        },
        created(){
            this.orderId = this.$route.params.orderId;
            console.log(this.orderId);
            this.getOrder();
        }
    }
</script>


